// COMPARTE //
.containerShareTop {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-family: $primaryFont;
    color: $metalColor;
    font-size: 18px;
    width: 100%;
    cursor: pointer;
    padding-bottom: 10px;
    border-top: 1px solid #d8d8d8;

}

.containerShareBottom {
    max-height: 0px;
    transition: max-height 0.5s;
    overflow: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .buttonShare {
        margin-top: 25px;
        margin-bottom: 30px;
        background-color:$primaryColor;
        width: 199px;
        height: 50px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        
        .iconSend {

            color: white;
        }

        .textButton {
            font-family: $primaryFont;
            font-size: 18px;
            color: white;
            padding-left: 9px;
        }
    }

}